import countTo from 'vue-count-to';
import { ObserveVisibility } from 'vue-observe-visibility'

export default {
  name: "Funfact",
  components: { countTo },
  directives: {
    ObserveVisibility
  },
  data() {
    return{
      startCounter: false
    }
  },
  methods: {
    onVisibilityChange (isVisible) {
      if (isVisible){
        this.startCounter = true;
      }

    },
  },
}
